<!-- 任务详情 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />

      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">任务详情</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top">
          <van-col class="dt-item-title">任务编号：</van-col>
          <div class="dt-item-content">{{task.TaskCode}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">任务标题：</van-col>
          <div class="dt-item-content">{{task.TaskTitle}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">任务内容：</van-col>
          <div class="dt-item-content">{{task.TaskContent}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">任务状态：</van-col>
          <div class="dt-item-content">{{task.TaskStatus | filteOrderTStatus}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">提交人：</van-col>
          <div class="dt-item-content">{{task.SubmitUserName}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">提交时间：</van-col>
          <div class="dt-item-content">{{task.SubmitTime | filteDate}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">审批人：</van-col>
          <div class="dt-item-content">{{task.ExamineUserName}}</div>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">发布时间：</van-col>
          <div class="dt-item-content">{{task.SendTime | filteDate}}</div>
        </van-row>
        <van-row class="dt-top"
                 @click="HaveRead">
          <van-col class="dt-item-title">已阅：</van-col>
          <div class="dt-item-content">{{haveRead}}</div>
        </van-row>
        <van-row class="dt-top"
                 @click="NotRead">
          <van-col class="dt-item-title">未阅：</van-col>
          <div class="dt-item-content">{{notRead}}</div>
        </van-row>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions,   // 任务查询
  GetTaskuser,                  // 任务下发记录查询
  UpDateTaskuser,               // 任务反馈接口
} from "@/api/testApi"
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      task: {},
      notRead: 0,
      haveRead: 0,
    };
  },
  filters: {
    filteDate (date) {
      if (date) {
        return date.replace("T", " ");
      }
    },
    filteOrderTStatus (data) {
      let all = {
        1: '待审核',
        2: '已审核',
        3: '已驳回',
        4: '已完成',
      }
      return all[data]
    },
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getInfo() // 获取详情信息

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //获取详情
    getInfo () {
      if (this.$route.query.formType == 1 || this.$route.query.formType == 2) {
        GetPageContentByConditions({ ID: this.$route.query.ID }).then(response => {
          this.task = response.Data.ContentList[0]
          this.getTaskuser() // 获取任务下发记录
          this.testCoupleBack() // 任务反馈
        })
      } else if (this.$route.query.formType == 3 || this.$route.query.formType == 4) {
        GetTaskuser({ ID: this.$route.query.ID }).then(response => {
          this.task = response.Data.ContentList[0]
          this.getTaskuser() // 获取任务下发记录
          this.testCoupleBack() // 任务反馈
        })
      }
    },
    // 任务下发记录查询
    getTaskuser () {
      let id = ""
      if (this.$route.query.formType == 1 || this.$route.query.formType == 2) {
        id = this.task.ID
      } else if (this.$route.query.formType == 3 || this.$route.query.formType == 4) {
        id = this.task.TaskId
      }
      let notReadInfo = {
        TaskId: id,
        ReadStatus: 0,  // 0未阅，1已阅
        PageSize: 1,
        PageIndex: 1
      }
      let haveReadInfo = {
        TaskId: id,
        ReadStatus: 1,  // 0未阅，1已阅
        PageSize: 1,
        PageIndex: 1
      }
      GetTaskuser(notReadInfo).then(response => {
        this.notRead = response.Data.TotalCount
      })
      GetTaskuser(haveReadInfo).then(response => {
        this.haveRead = response.Data.TotalCount
      })
    },
    // 任务反馈(将未阅改为已阅)
    testCoupleBack () {
      let info =
      {
        ID: this.task.ID,
        OrganizationId: null,
        TaskId: this.task.TaskId,
        BackInfo: "",
        BackTime: new Date(),
        ReadStatus: 1,
        ReceiveUserId: JSON.parse(localStorage.getItem("userInfo")).uid,
        ReceiveUserName: JSON.parse(localStorage.getItem("userInfo")).real_name,
        DealStatus: 1,
        Validity: 1
      }
      UpDateTaskuser(info).then(response => {
        console.log("任务反馈返回参数", response);
      })
    },

    // 判断用户是否为管理员
    getIsAdmin () {
      if (!localStorage.getItem("roleInfoList")) {
        return false
      }
      let roleInfoList = JSON.parse(localStorage.getItem("roleInfoList"))
      if (roleInfoList.indexOf("APProleKey") != -1) {
        return true
      } else {
        return false
      }
    },
    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    // 已阅
    HaveRead () {
      if (this.getIsAdmin()) {
        this.$router.push({ name: 'TaskHaveRead', query: { ID: this.task.ID } })
      }
    },
    // 未阅
    NotRead () {
      if (this.getIsAdmin()) {
        this.$router.push({ name: 'TaskNotRead', query: { ID: this.task.ID } })
      }
    },
    back () {
      this.$store.commit("active", 0)
      javascript: history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../../styles/dimens";
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
}
.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  // background-image: url("../../../assets/img/工单背景.png");
  background-image: url("../../../assets/img/背景图.png");
  background-size: 100% 100%;
  // background-attachment: fixed;
  // background-position: 0 px2rem(74);
  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    margin: 0 3%;
    margin-top: px2rem(29);
    overflow-y: auto;
    .dt-item-title {
      width: px2rem(90);
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #707070;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-content {
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #4c4c4c;
      line-height: px2rem(23);
      width: px2rem(200);
      float: right;
      padding-right: px2rem(25);
    }
    .dt-top {
      margin-top: px2rem(6);
    }
  }
}
</style>